nav {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 5rem rgba(0, 0, 0, 0.6);
}

nav > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-drop-down {
  background-color: #16171a;
  max-height: 100%;
  max-width: 100%;
  flex-grow: 1;
  text-align: center;
  font-size: 3rem;
  cursor: pointer;
}

.nav-settings {
  max-width: 10rem;
  max-height: 100%;
  flex-grow: 1;
  background-color: #1e5380;
}
